import React from 'react'
import { useSelector } from 'react-redux'
import { get } from 'lodash'

const translations = {
  en_GB: {
    header: {
      title: 'Exercise Routines',
      nav: {
        about: 'About',
        login: 'Login',
      },
    },
    routines: {
      jumbotron: {
        h1: 'Start your exercise by selecting one of the routines!',
        p:
          'Select one of the available routines and start your exercise! More routines will be available soonish',
      },
      routine: {
        buttons: {
          start: 'Start exercise!',
        },
      },
    },
    exercises: {
      jumbotron: {
        h1: 'Start your exercise by pressing start button!',
        p:
          'You can change the order of exercises by dragging them around. There will be a 5s break between exercises. Good Luck!',
        button: 'Start exercise!',
      },
      exercise: {
        duration: 'Duration'
      }
    },
    footer: null,
  },
  ru_RU: {
    header: {
      title: 'Упражнения',
      nav: {
        about: 'О нас',
        login: 'Вход',
      },
    },
    routines: {
      jumbotron: {
        h1: 'Начни упражнения выбрав один из представленных режимов!',
        p:
          'Чтобы начять упражнения, выбери один из представленных ниже режимов',
      },
      routine: {
        buttons: {
          start: 'Начять упражнения!',
        },
      },
    },
    exercises: {
      jumbotron: {
        h1: 'Начни упражнения нажав кнопку старт!',
        p:
          'Порядок упражнений можно изменить перетаскивая их пальцем. Между упражнениями будет 5-секундный перерыв. Физкульт привет!',
        button: 'Старт!',
      },
      exercise: {
        duration: 'Продолжительность'
      }
    },
    footer: null,
  },
}

function Translate(props) {
  const locales = useSelector((state) => state.locales)
  const selectedLocale = locales.find((locale) => locale.selected)

  const { lt, item } = props

  if (lt) {
    return <span>{get(translations, `${selectedLocale.code}.${lt}`)}</span>
  }

  if (item) {
    return (
      <span>
        {typeof item === 'string' ? item : get(item, selectedLocale.code)}
      </span>
    )
  }

  return <span>Undefined</span>
}

export default Translate
